import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch
} from "react-router-dom";
import Home from "Routes/Home";
import TV from "Routes/TV";
import Header from "Components/Header";
import Search from "Routes/Search";
import Detail from "Routes/Detail";

export default () => (
  <Router>
    <Header />
    <Switch>
      <Route
        path="https://joseoncoder.github.io/nomflix/"
        exact
        component={Home}
      />
      <Route
        path="https://joseoncoder.github.io/nomflix/tv"
        exact
        component={TV}
      />
      <Route
        path="https://joseoncoder.github.io/nomflix/search"
        exact
        component={Search}
      />
      <Route
        path="https://joseoncoder.github.io/nomflix/movie/:id"
        component={Detail}
      />
      <Route
        path="https://joseoncoder.github.io/nomflix/show/:id"
        component={Detail}
      />
      <Redirect from="*" to="/" />
    </Switch>
  </Router>
);
